            <img src="/static/imgs/banner1.jpg" width="100%" />
          </a-col>
          <a-col :span="8">
            <img src="/static/imgs/banner1.jpg" width="100%" />
          </a-col>
          <a-col :span="8">
            <img src="/static/imgs/banner1.jpg" width="100%" />
