            <div class="line"></div>
            <h1>信念是一道桥</h1>
            <div class="text-middle" style="textAlign:center">
              邮箱:1944522054@qq.com
              <br />手机号:17863129132
              <br />微信:17863129132
              <br />QQ:1944522054
            </div>
          </div>
        </div>
        <div class="img-bg2" style="width:60%"></div>
