    <!-- =================foot================= -->
    <div id="foot" class="foot flex-row">
      <div class="icons-list flex-row">
        <a-icon type="weibo-square" class="foot-icon" />
        <a-icon type="github" class="foot-icon" />
        <a-icon type="twitter" class="foot-icon" />
