</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      winHeight: window.innerHeight,
      is_bg: true,
      imgs: [
        { src: "/static/imgs/banner1.jpg" },
        { src: "/static/imgs/banner2.jpg" },
        { src: "/static/imgs/banner3.jpg" },
        { src: "/static/imgs/banner4.jpg" }
      ],
      visible: false,
      confirmLoading: false,
      step: {
        //此数据是控制动画快慢的
        type: Number,
        default: 50
      },
      form: this.$form.createForm(this),
      visible: false,
      current: ["login"],
      isLogin: true,
      checked1: false,
      checked2: false,
      checked3: false,
      tags: [
        "上海",
        "海南",
        "广州",
        "深圳",
        "香港",
        "澳门",
        "西藏",
        "东北",
        "欧洲",
        "美洲",
        "澳洲",
        "非洲"
      ],
      selectedTags: []
    };
  },
  mounted() {
    // this.$refs.head.style.height = this.winHeight + "px";
    this.$refs.head.style.height = document.body.scrollWidth;
  },
  created() {},
  methods: {
    toItemA() {
      
    },
    toItemB() {
      this.$router.push('/myworld');
    },
    toItemC() {
      
    },
    toItemD() {

    },
    handleChange(tag, checked) {
      const { selectedTags } = this;
      const nextSelectedTags = checked
        ? [...selectedTags, tag]
        : selectedTags.filter(t => t !== tag);
      console.log("You are interested in: ", nextSelectedTags);
      this.selectedTags = nextSelectedTags;
    },
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    handleOk(e) {
      this.ModalText = "The modal will be closed after two seconds";
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
    },
    handleCancel(e) {
      console.log("Clicked cancel button");
      this.visible = false;
    },
    goTop() {
      let distance =
        document.documentElement.scrollTop || document.body.scrollTop; //获得当前高度

      let step = distance / 50; //每步的距离
      (function jump() {
        if (distance > 0) {
          distance -= step;
          window.scrollTo(0, distance);
          setTimeout(jump, 14);
        }
      })();
    },
    goAbout(target) {
      let distance =
        document.documentElement.scrollTop || document.body.scrollTop; //获得当前高度
      target = document.getElementById(target).scrollHeight;
      let step = target / 50; //每步的距离
      (function jump() {
        if (distance <= target) {
          distance += step;
          window.scrollTo(0, distance);
          setTimeout(jump, 14);
        }
      })();
    },
    goContact() {
      let distance =
        document.documentElement.scrollTop || document.body.scrollTop; //获得当前高度
      var target = document.getElementById("all").scrollHeight;
      let step = target / 50; //每步的距离
      (function jump() {
        if (distance <= target) {
          distance += step;
          window.scrollTo(0, distance);
          setTimeout(jump,20);      
        }
      })();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.menu {
