  margin: 0 auto;
  margin-top: 3rem;
  font-size: 1rem;
  font-weight: 400;
  color: rgb(255, 255, 255);
}
.body-bg {
  background-color: rgb(46, 196, 182);
  padding: 3rem 8rem 2rem 1rem;
}
.body-bg2 {
  background-color: rgba(40, 180, 166, 0.932);
  padding: 7rem 8rem 2rem 1rem;
}
.hello {
