}
.nav-button {
  /* padding: 0.3rem; */
  border-radius: 4px;
  margin-left: 0.7rem;
  padding-left: 0.4rem;
  padding-right: 0.4rem;
  cursor: pointer;
}
.nav-button:hover {
  background: #ffffff;
}
.nav-button > h1:hover {
  color: rgba(0, 0, 0, 0.726);
}
h1 {
  font-size: 1.4rem;
  color: rgb(255, 255, 255);
  font-weight: 600;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
  margin: 0;
}
.head-content {
  /* z-index: -1; */
  /* position: fixed; */
  height: 100%;
  
}
.head-content-head {
  z-index: 3;
  width: 100%;
  position: fixed;
  padding: 0 1.3rem;
  background-color: #0000002d;
}
.head-content-content {
  position: absolute;
}
.flex-col {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
  /* justify-items: center; */
}
.header {
  width: 100%;
  background-repeat: no-repeat;
}
</style>



